.Cart{
    position: relative;
    color: #222;

    &__title{
        margin-left: 5%;
    }

    &__list{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        padding: 0;

        &__listItem{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 70%;
            list-style-type: none;
            padding: 3%;
            margin-bottom: 3%;
            background-color: #ffebc1;
            border-radius: 15px;
            border: 3px solid white;
            font-weight: 600;

            &__itemContainer{
                width: 100%;
                padding: 0 3%;
                display: flex;
                justify-content: space-evenly;
                align-items: center;
                flex-wrap: wrap;

                &__itemImageContainer{
                    width: 50px;
                    height: 50px;
                    background-color: #eee;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border: 2px solid #fdbc0b;
                    border-radius: 10px;

                    &__image{
                        max-height: 40px;
                    }

                }

                &__itemTitle{
                    font-size: large;
                    font-weight: 600;
                    text-align: center;
                    margin: 0 5px;
                }

                &__itemPricing{
                    width: fit-content;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    &__itemAmount{
                        background-color: #ff9346;
                        border: 1px solid white;
                        border-radius: 5px;
                        padding: 0 3px;
                    }

                    &__itemFinalPrice{
                        font-size: large;
                        font-weight: 700;
                        margin-left: 3px;
                    }
                }
            }

            &__eraseButton{
                height: 30px;
                background-color: #ff3700;
                border-radius: 5px;
                border: 0px;
                padding: 5px 10px;
                color: white;
                font-weight: bold;
            }

            &__eraseButton:hover{
                background-color: #ff6000;
            }
        }

        @media screen and (max-width:680px) {
            &__listItem{
                justify-content: center;
                flex-wrap: wrap;
                min-height: fit-content;

                &__itemContainer{
                    justify-content: center;

                    &__itemPricing{
                    width: 95%;
                    justify-content: center;
                    flex-wrap: wrap;
                    }
                    margin-bottom: 3%;
                }

                &__eraseButton{
                    width: 90%;
                    height: 30px;
                    background-color: #ff3700;
                    border-radius: 5px;
                    border: 0px;
                    padding: 5px 10px;
                    color: white;
                    font-weight: bold;
                }
            }
        }

        &__totalContainer{
            width: 100%;
            display: flex;
            justify-content: center;
            font-size: x-large;
            font-weight: bold;

            &__total{
                margin-left: 1%;
            }
        }

        &__goToForm{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            &__container__button{
                margin-top: 20%;
                background-color: #ff9346;
                padding: 10px 20px;
                border-radius: 10px;
                border: 3px solid white ;
                font-size: large;
                font-weight: 700;
                color: #eee;
            }
        }
    }

    &__emptyContainer{
        height: 50vh;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        color: #eee;
        font-size: larger;

        &__goToHome{
            margin-top: 10%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;

            &__button{
                background-color: #ff9346;
                padding: 10px 20px;
                border-radius: 10px;
                border: 3px solid white ;
                font-size: large;
                font-weight: 700;
                color: #eee;
            }
        }
    }
}